import { request } from 'utils';

const prefix = '/api/education';

export const getClassAdditionSubject = ({
  classId,
  facultyId,
  studyFieldId,
  yearEduId,
  semesterId,
  registerSessionId,
  isRegister,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/GetClassAdditionSubject',
    params: {
      classId,
      facultyId,
      studyFieldId,
      yearEduId,
      semesterId,
      registerSessionId,
      isRegister,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const deleteRegisterClassCreditEnrollment = reqBody =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/DeleteRegisterClassCreditEnrollment',
    method: 'DELETE',
    data: reqBody,
  });

export const createRegisterClassCreditEnrollment = reqBody =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/CreateRegisterClassCreditEnrollment',
    method: 'POST',
    data: reqBody,
  });



