import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getClasss } from 'services/class';
import { getDataForSelect } from 'services/registerConfig';
import { BtnClear, BtnSearch } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const queryParams = getQueryParams();
    delete queryParams._time;
    form.setFieldsValue(queryParams);
  };

  handleReset = () => {
    const { resetFields } = this.props.form;
    resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [requiredRule],
      },
      facultyId: {
        rules: [requiredRule],
      },
      courseId: {
        rules: [requiredRule],
      },
      studyFieldId: {
        rules: [requiredRule],
      },
    };
    const trainTypeId = getFieldValue('trainTypeId');
    const yearEduId = getFieldValue('yearEduId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            {...formItemLayout}
            optionFieldDecorator={optionFieldDecorator}
            classes
            column={6}
          />
        </Row>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lớp" required>
              {getFieldDecorator('classId', { rules: [requiredRule] })(
                <SelectAjax
                  typeValue="int"
                  service={getClasss}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                  allowGetData={!!studyFieldId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Năm học" required>
              {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Học kì" required>
              {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  allowGetData={!!yearEduId}
                  params={{ yearEduId }}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Kỳ đăng ký" required>
              {getFieldDecorator('registerSessionId', { rules: [requiredRule] })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getDataForSelect}
                  allowGetData={!!trainTypeId}
                  params={{ trainTypeId }}
                  typeValue="int"
                  keyLabel="label"
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={6} offset={18}>
            <BtnSearch htmlType="submit" style={{ marginRight: 5 }} />
            <BtnClear onClick={this.handleReset} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



