import React, { Component } from 'react';
import { Row, Col, Divider } from 'antd';
import { TableData } from 'components/common';
import { BtnDelete } from 'components/common/button';
import {
  getClassAdditionSubject,
  deleteRegisterClassCreditEnrollment,
} from 'services/classAdditionalRegistration';

class TableAddRegisterSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {
      totalCredits: 0,
      totalSubjects: 0,
      selectedRowKeys: [],
      selectedRows: [],
      removing: false,
    };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys, selectedRows });
  };

  getClassAdditionSubject = async paramSearch => {
    const res = await getClassAdditionSubject(paramSearch);
    const { items } = res.data;
    const totalCredits = items.map(item => item.noOfCredit).reduce((a, b) => a + b, 0);
    const totalSubjects = items.length;
    this.setState({ totalCredits, totalSubjects });
    return res;
  };

  columns = [
    {
      title: 'Mã học phần',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
    },
    {
      title: 'Tên học phần',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Số TC',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tên lớp tín chỉ',
      dataIndex: 'classCreditCode',
      key: 'classCreditCode',
    },
    {
      title: 'Lịch học',
      dataIndex: 'schedule',
      key: 'schedule',
    },
    {
      title: 'Giáo viên',
      dataIndex: 'teacherName',
      key: 'teacherName',
    },
    {
      title: 'Phòng học',
      dataIndex: 'roomName',
      key: 'roomName',
    },
    {
      title: 'Còn trống',
      dataIndex: 'noOfEmptySlot',
      key: 'noOfEmptySlot',
    },
  ];

  handleDelete = async () => {
    this.setState({ removing: true });
    const { selectedRows } = this.state;
    const { doReload, paramSearch } = this.props;
    const deleteCCEs = selectedRows.map(selectedRow => ({
      ccsId: selectedRow.ccsId,
      classId: paramSearch.classId,
    }));
    const reqBody = {
      userLogged: '3fa85f64-5717-4562-b3fc-2c963f66afa6',
      deleteCCEs,
    };
    try {
      await deleteRegisterClassCreditEnrollment(reqBody);
      this.setState({ selectedRowKeys: [], selectedRows: [] });
      doReload();
    } catch (err) {
      console.error(err);
    }
    this.setState({ removing: false });
  };

  render() {
    const { totalCredits, totalSubjects, selectedRows, selectedRowKeys, removing } = this.state;
    const {
      paramSearch,
      // student,
      reload,
    } = this.props;
    paramSearch.isRegister = true;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <br />
        <Row gutter={24}>
          <Col span={6}>
            Số học phần: <b>{totalSubjects}</b>
          </Col>
          <Col span={6}>
            Số TC: <b>{totalCredits}</b>
          </Col>
          <Col span={6}>
            Số tiền phải nộp: <b>0</b>
          </Col>
        </Row>
        <br />
        <Divider>Danh sách môn đã đăng ký</Divider>
        <BtnDelete
          onClick={this.handleDelete}
          title="Xóa"
          disabled={selectedRows.length === 0}
          loading={removing}
        />
        <TableData
          reload={reload}
          rowSelection={rowSelection}
          service={this.getClassAdditionSubject}
          paramSearch={paramSearch}
          columns={this.columns}
          allowGetData={
            !!paramSearch.classId &&
            !!paramSearch.facultyId &&
            !!paramSearch.studyFieldId &&
            !!paramSearch.yearEduId &&
            !!paramSearch.semesterId &&
            !!paramSearch.registerSessionId
          }
          rowKey="syllableCode"
        />
      </div>
    );
  }
}

export default TableAddRegisterSelected;



