import React, { Component } from 'react';
import { TableData } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Divider } from 'antd';
import {
  getClassAdditionSubject,
  createRegisterClassCreditEnrollment,
} from 'services/classAdditionalRegistration';

class TableAddRegisterNotSelected extends Component {
  constructor(props) {
    super(props);
    this.state = { selectedRowKeys: [], selectedRows: [], adding: false };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys, selectedRows });
  };

  columns = [
    {
      title: 'Mã học phần',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
    },
    {
      title: 'Tên học phần',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Số TC',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tên lớp tín chỉ',
      dataIndex: 'classCreditCode',
      key: 'classCreditCode',
    },
    {
      title: 'Lịch học',
      dataIndex: 'schedule',
      key: 'schedule',
    },
    {
      title: 'Giáo viên',
      dataIndex: 'teacherName',
      key: 'teacherName',
    },
    {
      title: 'Phòng học',
      dataIndex: 'roomName',
      key: 'roomName',
    },
    {
      title: 'Còn trống',
      dataIndex: 'noOfEmptySlot',
      key: 'noOfEmptySlot',
    },
  ];

  handleAdd = async () => {
    this.setState({ adding: true });
    const { selectedRows } = this.state;
    const { doReload, paramSearch } = this.props;
    const createCCEs = selectedRows.map(selectedRow => ({
      ccsId: selectedRow.ccsId,
    }));
    const reqBody = {
      userLogged: '3fa85f64-5717-4562-b3fc-2c963f66afa6',
      classId: paramSearch.classId,
      createCCEs,
    };
    try {
      await createRegisterClassCreditEnrollment(reqBody);
      this.setState({ selectedRowKeys: [], selectedRows: [] });
      doReload();
    } catch (err) {
      console.error(err);
    }
    this.setState({ adding: false });
  };

  render() {
    const { selectedRows, selectedRowKeys, adding } = this.state;
    const { paramSearch, reload } = this.props;
    paramSearch.isRegister = false;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <br />
        <Divider>Danh sách môn chưa đăng ký</Divider>
        <BtnAddNew
          onClick={this.handleAdd}
          title="Thêm"
          disabled={selectedRows.length === 0}
          loading={adding}
        />
        <TableData
          reload={reload}
          rowSelection={rowSelection}
          service={getClassAdditionSubject}
          paramSearch={paramSearch}
          columns={this.columns}
          allowGetData={
            !!paramSearch.classId &&
            !!paramSearch.facultyId &&
            !!paramSearch.studyFieldId &&
            !!paramSearch.yearEduId &&
            !!paramSearch.semesterId &&
            !!paramSearch.registerSessionId
          }
        />
      </div>
    );
  }
}

export default TableAddRegisterNotSelected;



